import React, { useState, useEffect } from 'react';
import { Toaster } from 'react-hot-toast';
import styled from 'styled-components';
import Header from './components/Header';
import MapContainer from './components/MapContainer';
import ControlPanel from './components/ControlPanel';
import DataAnalysisModal from './components/DataAnalysisModal';
import LoadingSpinner from './components/LoadingSpinner';
import ResponsiveLayout from './components/ResponsiveLayout';
import { ClimateDataProvider } from './context/ClimateDataContext';
import { ThemeProvider } from './context/ThemeContext';

const AppContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: var(--background-color);
  overflow: hidden;
`;

const MainContent = styled.div`
  display: flex;
  flex: 1;
  overflow: hidden;
  gap: 0;
  min-height: 0;
  
  @media (max-width: 768px) {
    flex-direction: column;
  }
`;

const MapSection = styled.div`
  flex: 1;
  position: relative;
  background: var(--card-background);
  border-radius: 12px 0 0 0;
  overflow: hidden;
  min-width: 0;
  min-height: 300px;
  border: 1px solid var(--border-color);
  
  @media (max-width: 768px) {
    border-radius: 0;
    min-height: 250px;
  }
`;


const App: React.FC = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [isMobile, setIsMobile] = useState(false);
  const [isDataModalOpen, setIsDataModalOpen] = useState(false);
  const [selectionMode, setSelectionMode] = useState<'point' | 'rectangle' | 'circle' | null>(null);
  const [selectedArea, setSelectedArea] = useState<{
    bounds: any;
    name: string;
    selectionType?: string;
    circleParams?: { center: [number, number]; radiusKm: number };
  } | null>(null);

  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    
    checkMobile();
    window.addEventListener('resize', checkMobile);
    
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  const sidebar = (
    <ControlPanel 
      selectedArea={selectedArea}
      onLoadingChange={setIsLoading}
      onOpenDataAnalysis={() => setIsDataModalOpen(true)}
    />
  );

      return (
        <ThemeProvider>
          <ClimateDataProvider>
            <AppContainer>
              <Header selectionMode={selectionMode} />
              <MainContent>
                <MapSection>
                  <MapContainer 
                    onAreaSelect={setSelectedArea}
                    selectedArea={selectedArea}
                    onSelectionModeChange={setSelectionMode}
                  />
                  {isLoading && <LoadingSpinner />}
                </MapSection>
                <ResponsiveLayout
                  isMobile={isMobile}
                  sidebar={sidebar}
                >
                  <div style={{ flex: 1 }} />
                </ResponsiveLayout>
              </MainContent>
              <Toaster 
                position="top-right"
                toastOptions={{
                  duration: 4000,
                  style: {
                    background: 'var(--card-background)',
                    color: 'var(--text-color)',
                    borderRadius: '8px',
                    border: '1px solid var(--border-color)',
                  },
                }}
              />
              
              <DataAnalysisModal
                isOpen={isDataModalOpen}
                onClose={() => setIsDataModalOpen(false)}
                selectedArea={selectedArea}
              />
            </AppContainer>
          </ClimateDataProvider>
        </ThemeProvider>
      );
};

export default App;
