// Fix para ícones do Leaflet
delete (Icon.Default.prototype as any)._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon-2x.png',
  iconUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-icon.png',
  shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.9.4/images/marker-shadow.png',
});

interface MapContainerProps {
  onAreaSelect: (area: { bounds: any; name: string; selectionType?: string; circleParams?: { center: [number, number]; radiusKm: number } } | null) => void;
  selectedArea: { bounds: any; name: string; selectionType?: string; circleParams?: { center: [number, number]; radiusKm: number } } | null;
  onSelectionModeChange?: (mode: 'point' | 'rectangle' | 'circle' | null) => void;
}

interface HeatmapData {
  lat: number;
  lon: number;
  value: number;
}

interface HeatmapLayerProps {
  data: HeatmapData[];
  variable: string;
  bounds: any;
}

// Componente para renderizar heatmaps
const HeatmapLayer: React.FC<HeatmapLayerProps> = ({ data, variable }) => {
  
  // Função para obter cor baseada no valor
  const getColor = (value: number, variable: string) => {
    switch (variable) {
      case 'lst':
        // LST: Azul (frio) → Laranja (quente) - Esquema SOLARIS
        if (value < 20) return '#1e40af'; // Azul escuro
        if (value < 25) return '#3b82f6'; // Azul
        if (value < 30) return '#10b981'; // Verde
        if (value < 35) return '#ffff00'; // Amarelo
        if (value < 40) return '#ff8c00'; // Laranja
        return '#ff4500'; // Laranja vermelho
      
      case 'ndvi':
        // NDVI: Marrom (solo) → Verde (vegetação)
        if (value < 0.1) return '#8b5a2b'; // Marrom escuro
        if (value < 0.3) return '#d97706'; // Marrom claro
        if (value < 0.5) return '#ffff00'; // Amarelo
        if (value < 0.7) return '#22c55e'; // Verde claro
        return '#16a34a'; // Verde escuro
      
      case 'ndbi':
        // NDBI: Verde (natural) → Laranja (construído) - Esquema SOLARIS
        if (value < 0.1) return '#16a34a'; // Verde
        if (value < 0.3) return '#84cc16'; // Verde claro
        if (value < 0.5) return '#ffff00'; // Amarelo
        if (value < 0.7) return '#ff8c00'; // Laranja
        return '#ff4500'; // Laranja vermelho
      
      case 'ndwi':
        // NDWI: Marrom (seco) → Azul (água)
        if (value < 0.1) return '#8b5a2b'; // Marrom
        if (value < 0.3) return '#d97706'; // Marrom claro
        if (value < 0.5) return '#06b6d4'; // Ciano
        if (value < 0.7) return '#0ea5e9'; // Azul claro
        return '#0284c7'; // Azul
      
      case 'popDens':
        // POP_DENS: Verde (baixa) → Laranja (alta) - Esquema SOLARIS
        if (value < 100) return '#16a34a'; // Verde
        if (value < 1000) return '#22c55e'; // Verde claro
        if (value < 5000) return '#ffff00'; // Amarelo
        if (value < 10000) return '#ff8c00'; // Laranja
        return '#ff4500'; // Laranja vermelho
      
      case 'nightLights':
        // NIGHT_LIGHTS: Preto (escuro) → Laranja (iluminado) - Esquema SOLARIS
        if (value < 20) return '#1f2937'; // Preto
        if (value < 50) return '#374151'; // Cinza escuro
        if (value < 100) return '#6b7280'; // Cinza
        if (value < 200) return '#ff8c00'; // Laranja
        return '#ff4500'; // Laranja vermelho
      
      case 'heatIslandVulnerability':
        // VULNERABILIDADE: Verde (baixo) → Vermelho (alto)
        if (value < 20) return '#16a34a'; // Verde
        if (value < 40) return '#22c55e'; // Verde claro
        if (value < 60) return '#ffff00'; // Amarelo
        if (value < 80) return '#ff8c00'; // Laranja
        return '#ef4444'; // Vermelho
      
      case 'heatIslandPrediction':
        // PREVISÃO: Azul (baixo) → Vermelho (alto)
        if (value < 20) return '#1e40af'; // Azul
        if (value < 40) return '#3b82f6'; // Azul claro
        if (value < 60) return '#ffff00'; // Amarelo
        if (value < 80) return '#ff8c00'; // Laranja
        return '#ef4444'; // Vermelho
      
      default:
        return '#6b7280'; // Cinza padrão
    }
  };

  // Função para obter tamanho do círculo baseado no valor
  const getRadius = (value: number, variable: string) => {
    const baseRadius = 8;
    const maxRadius = 20;
    
    switch (variable) {
      case 'lst':
        return Math.max(baseRadius, Math.min(maxRadius, baseRadius + (value - 20) * 0.5));
      case 'ndvi':
        return Math.max(baseRadius, Math.min(maxRadius, baseRadius + value * 10));
      case 'ndbi':
        return Math.max(baseRadius, Math.min(maxRadius, baseRadius + value * 15));
      case 'ndwi':
        return Math.max(baseRadius, Math.min(maxRadius, baseRadius + value * 12));
      case 'popDens':
        return Math.max(baseRadius, Math.min(maxRadius, baseRadius + (value / 1000) * 2));
      case 'nightLights':
        return Math.max(baseRadius, Math.min(maxRadius, baseRadius + (value / 50) * 2));
      default:
        return baseRadius;
    }
  };

  return (
    <>
      {data.map((point, index) => (
        <CircleMarker
          key={`${variable}-${index}`}
          center={[point.lat, point.lon]}
          radius={getRadius(point.value, variable)}
          pathOptions={{
            fillColor: getColor(point.value, variable),
            color: getColor(point.value, variable),
            weight: 1,
            opacity: 0.8,
            fillOpacity: 0.6
          }}
        >
          <Popup>
            <div style={{ minWidth: '150px' }}>
              <h4 style={{ margin: '0 0 8px 0', fontSize: '14px', fontWeight: '600' }}>
                {variable.toUpperCase()}
              </h4>
              <p style={{ margin: '4px 0', fontSize: '12px' }}>
                <strong>Valor:</strong> {point.value.toFixed(2)}
              </p>
              <p style={{ margin: '4px 0', fontSize: '12px' }}>
                <strong>Coordenadas:</strong> {point.lat.toFixed(4)}, {point.lon.toFixed(4)}
              </p>
            </div>
          </Popup>
        </CircleMarker>
      ))}
    </>
  );
};

