interface MapEventsProps {
  onAreaSelect: (area: { bounds: any; name: string; selectionType?: string; circleParams?: { center: [number, number]; radiusKm: number } } | null) => void;
  selectionMode: 'point' | 'rectangle' | 'circle' | null;
  setSelectionMode: (mode: 'point' | 'rectangle' | 'circle' | null) => void;
}

const MapEvents: React.FC<MapEventsProps> = ({ onAreaSelect, selectionMode, setSelectionMode }) => {
  const [startPoint, setStartPoint] = useState<[number, number] | null>(null);
  const [currentPoint, setCurrentPoint] = useState<[number, number] | null>(null);

  useMapEvents({
    click: (e) => {
      if (selectionMode === 'point') {
        const latlng = e.latlng;
        onAreaSelect({
          bounds: {
            north: latlng.lat + 0.01,
            south: latlng.lat - 0.01,
            east: latlng.lng + 0.01,
            west: latlng.lng - 0.01
          },
          name: `Ponto: ${latlng.lat.toFixed(4)}, ${latlng.lng.toFixed(4)}`,
          selectionType: 'point'
        });
        setSelectionMode(null);
      } else if (selectionMode === 'rectangle') {
        if (!startPoint) {
          setStartPoint([e.latlng.lat, e.latlng.lng]);
          setCurrentPoint([e.latlng.lat, e.latlng.lng]);
        } else {
          const bounds = {
            north: Math.max(startPoint[0], e.latlng.lat),
            south: Math.min(startPoint[0], e.latlng.lat),
            east: Math.max(startPoint[1], e.latlng.lng),
            west: Math.min(startPoint[1], e.latlng.lng)
          };
          
          onAreaSelect({
            bounds,
            name: `Retângulo: ${bounds.south.toFixed(4)}-${bounds.north.toFixed(4)}, ${bounds.west.toFixed(4)}-${bounds.east.toFixed(4)}`,
            selectionType: 'rectangle'
          });
          setStartPoint(null);
          setCurrentPoint(null);
          setSelectionMode(null);
        }
      } else if (selectionMode === 'circle') {
        if (!startPoint) {
          setStartPoint([e.latlng.lat, e.latlng.lng]);
          setCurrentPoint([e.latlng.lat, e.latlng.lng]);
        } else {
          // Calcular raio baseado na distância entre os pontos
          const R = 6371e3; // Raio da Terra em metros
          const φ1 = startPoint[0] * Math.PI/180;
          const φ2 = e.latlng.lat * Math.PI/180;
          const Δφ = (e.latlng.lat - startPoint[0]) * Math.PI/180;
          const Δλ = (e.latlng.lng - startPoint[1]) * Math.PI/180;

          const a = Math.sin(Δφ/2) * Math.sin(Δφ/2) +
                    Math.cos(φ1) * Math.cos(φ2) *
                    Math.sin(Δλ/2) * Math.sin(Δλ/2);
          const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
          const distance = R * c; // Distância em metros
          
          const radiusKm = distance / 1000;
          const bounds = {
            north: startPoint[0] + (radiusKm / 111),
            south: startPoint[0] - (radiusKm / 111),
            east: startPoint[1] + (radiusKm / (111 * Math.cos(startPoint[0] * Math.PI / 180))),
            west: startPoint[1] - (radiusKm / (111 * Math.cos(startPoint[0] * Math.PI / 180)))
          };
          
          onAreaSelect({
            bounds,
            name: `Círculo: Centro ${startPoint[0].toFixed(4)}, ${startPoint[1].toFixed(4)}, Raio ${radiusKm.toFixed(2)}km`,
            selectionType: 'circle',
            circleParams: {
              center: [startPoint[0], startPoint[1]],
              radiusKm: radiusKm
            }
          });
          setStartPoint(null);
          setCurrentPoint(null);
          setSelectionMode(null);
        }
      }
    },
    mousemove: (e) => {
      if (selectionMode === 'rectangle' || selectionMode === 'circle') {
        if (startPoint) {
          setCurrentPoint([e.latlng.lat, e.latlng.lng]);
        }
      }
    }
  });

  // Renderizar preview da seleção
  if (startPoint && currentPoint && selectionMode) {
    if (selectionMode === 'rectangle') {
      const bounds = [
        [Math.min(startPoint[0], currentPoint[0]), Math.min(startPoint[1], currentPoint[1])] as [number, number],
        [Math.max(startPoint[0], currentPoint[0]), Math.max(startPoint[1], currentPoint[1])] as [number, number]
      ];
      return <Rectangle bounds={bounds} pathOptions={{ color: '#ff8c00', weight: 3, fillOpacity: 0.1, dashArray: '5, 5' }} />;
    } else if (selectionMode === 'circle') {
      // Calcular raio em tempo real
      const R = 6371e3;
      const φ1 = startPoint[0] * Math.PI/180;
      const φ2 = currentPoint[0] * Math.PI/180;
      const Δφ = (currentPoint[0] - startPoint[0]) * Math.PI/180;
      const Δλ = (currentPoint[1] - startPoint[1]) * Math.PI/180;

      const a = Math.sin(Δφ/2) * Math.sin(Δφ/2) +
                Math.cos(φ1) * Math.cos(φ2) *
                Math.sin(Δλ/2) * Math.sin(Δλ/2);
      const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
      const distance = R * c;
      
      return <Circle center={startPoint} radius={distance} pathOptions={{ color: '#ff8c00', weight: 3, fillOpacity: 0.1, dashArray: '5, 5' }} />;
    }
  }

  return null;
};

const MapContainer: React.FC<MapContainerProps> = ({ onAreaSelect, selectedArea, onSelectionModeChange }) => {
  const [selectionMode, setSelectionMode] = useState<'point' | 'rectangle' | 'circle' | null>(null);
  const { climateData } = useClimateData();
  const { theme } = useTheme();

  // Notificar mudanças no modo de seleção
  React.useEffect(() => {
    if (onSelectionModeChange) {
      onSelectionModeChange(selectionMode);
    }
  }, [selectionMode, onSelectionModeChange]);

  const handleClearSelection = () => {
    onAreaSelect(null);
    setSelectionMode(null);
  };

  const getBoundsFromArea = (area: { bounds: any; name: string }) => {
    const { north, south, east, west } = area.bounds;
    return new LatLngBounds(
      [south, west],
      [north, east]
    );
  };

  return (
    <MapWrapper>
      <LeafletMap
        center={[-15.7801, -47.9292]} // Centro do Brasil
        zoom={5}
        style={{ height: '100%', width: '100%' }}
      >
        <TileLayer
          url={theme === 'dark' 
            ? "https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png"
            : "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          }
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />
        
        <MapEvents
          onAreaSelect={onAreaSelect}
          selectionMode={selectionMode}
          setSelectionMode={setSelectionMode}
        />

        {selectedArea && (
          <>
            {selectedArea.selectionType === 'circle' && selectedArea.circleParams ? (
              <Circle
                center={selectedArea.circleParams.center}
                radius={selectedArea.circleParams.radiusKm * 1000} // Converter km para metros
                pathOptions={{ color: '#3b82f6', fillColor: '#3b82f6', fillOpacity: 0.2 }}
              />
            ) : (
              <Rectangle
                bounds={getBoundsFromArea(selectedArea)}
                pathOptions={{ color: '#3b82f6', fillColor: '#3b82f6', fillOpacity: 0.2 }}
              />
            )}
          </>
        )}

        {/* Renderizar heatmaps para cada variável */}
        {climateData && selectedArea && Object.entries(climateData).map(([variable, data]) => (
          <HeatmapLayer
            key={variable}
            data={data}
            variable={variable}
            bounds={selectedArea.bounds}
          />
        ))}
      </LeafletMap>

      <MapControls>
        <ControlButton
          active={selectionMode === 'point'}
          onClick={() => setSelectionMode(selectionMode === 'point' ? null : 'point')}
        >
          <MapPin size={20} />
          {selectionMode === 'point' ? 'Clique no mapa' : 'Ponto'}
        </ControlButton>
        <ControlButton
          active={selectionMode === 'rectangle'}
          onClick={() => setSelectionMode(selectionMode === 'rectangle' ? null : 'rectangle')}
        >
          <Square size={20} />
          {selectionMode === 'rectangle' ? 'Clique 2 pontos' : 'Retângulo'}
        </ControlButton>
        <ControlButton
          active={selectionMode === 'circle'}
          onClick={() => setSelectionMode(selectionMode === 'circle' ? null : 'circle')}
        >
          <CircleIcon size={20} />
          {selectionMode === 'circle' ? 'Clique 2 pontos' : 'Círculo'}
        </ControlButton>
        <ControlButton onClick={handleClearSelection}>
          Limpar Seleção
        </ControlButton>
      </MapControls>


      {selectedArea && (
        <AreaInfo>
          <AreaTitle>Área Selecionada</AreaTitle>
          <AreaDetails>
            <div><strong>Nome:</strong> {selectedArea.name}</div>
            <div><strong>Norte:</strong> {selectedArea.bounds.north.toFixed(4)}°</div>
            <div><strong>Sul:</strong> {selectedArea.bounds.south.toFixed(4)}°</div>
            <div><strong>Leste:</strong> {selectedArea.bounds.east.toFixed(4)}°</div>
            <div><strong>Oeste:</strong> {selectedArea.bounds.west.toFixed(4)}°</div>
          </AreaDetails>
        </AreaInfo>
      )}

      {climateData && selectedArea && (
        <Legend>
          <LegendTitle>Legenda dos Dados</LegendTitle>
          {Object.keys(climateData).map(variable => {
            const getVariableInfo = (varName: string) => {
              switch (varName) {
                case 'lst':
                  return { name: 'Temperatura (LST)', colors: ['#1e40af', '#ff4500'], labels: ['Frio', 'Quente'] };
                case 'ndvi':
                  return { name: 'Vegetação (NDVI)', colors: ['#8b5a2b', '#16a34a'], labels: ['Solo', 'Vegetação'] };
                case 'ndbi':
                  return { name: 'Construção (NDBI)', colors: ['#16a34a', '#ff4500'], labels: ['Natural', 'Construído'] };
                case 'ndwi':
                  return { name: 'Água (NDWI)', colors: ['#8b5a2b', '#0284c7'], labels: ['Seco', 'Água'] };
                case 'popDens':
                  return { name: 'População', colors: ['#16a34a', '#ff4500'], labels: ['Baixa', 'Alta'] };
                case 'nightLights':
                  return { name: 'Luzes Noturnas', colors: ['#1f2937', '#ff4500'], labels: ['Escuro', 'Iluminado'] };
                case 'heatIslandVulnerability':
                  return { 
                    name: 'Vulnerabilidade Atual', 
                    colors: ['#16a34a', '#22c55e', '#ffff00', '#ff8c00', '#ef4444'], 
                    labels: ['Baixo', 'Médio', 'Moderado', 'Alto', 'Crítico'],
                    interpretation: 'Risco de Ilha de Calor'
                  };
                case 'heatIslandPrediction':
                  return { 
                    name: 'Previsão 5 Anos', 
                    colors: ['#1e40af', '#3b82f6', '#ffff00', '#ff8c00', '#ef4444'], 
                    labels: ['Baixo', 'Médio', 'Moderado', 'Alto', 'Crítico'],
                    interpretation: 'Risco Futuro de Ilha de Calor'
                  };
                default:
                  return { name: varName.toUpperCase(), colors: ['#6b7280', '#6b7280'], labels: ['Baixo', 'Alto'] };
              }
            };

            const info = getVariableInfo(variable);
            return (
              <div key={variable}>
                <LegendItem>
                  <LegendColor color={info.colors[0]} />
                  <LegendText>{info.name}</LegendText>
                </LegendItem>
                {info.interpretation && (
                  <div style={{ marginLeft: '24px', marginBottom: '8px' }}>
                    <div style={{ fontSize: '0.7rem', color: '#9ca3af', marginBottom: '4px' }}>
                      {info.interpretation}:
                    </div>
                    <div style={{ display: 'flex', flexWrap: 'wrap', gap: '6px' }}>
                      {info.colors.map((color, index) => (
                        <div key={index} style={{ display: 'flex', alignItems: 'center', gap: '4px' }}>
                          <div style={{ 
                            width: '10px', 
                            height: '10px', 
                            borderRadius: '50%', 
                            backgroundColor: color,
                            border: '1px solid #444'
                          }} />
                          <span style={{ fontSize: '0.65rem', color: '#e0e0e0' }}>
                            {info.labels[index]}
                          </span>
                        </div>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            );
          })}
        </Legend>
      )}
    </MapWrapper>
  );
};

export default MapContainer;
