const VARIABLES = [
  {
    id: 'lst',
    name: 'LST',
    description: 'Temperatura da Superfície',
    icon: Thermometer,
    color: '#ef4444',
    defaultMin: 20,
    defaultMax: 45
  },
  {
    id: 'ndvi',
    name: 'NDVI',
    description: 'Índice de Vegetação',
    icon: TreePine,
    color: '#10b981',
    defaultMin: 0.3,
    defaultMax: 1.0
  },
  {
    id: 'ndbi',
    name: 'NDBI',
    description: 'Índice de Construção',
    icon: Settings,
    color: '#f59e0b',
    defaultMin: 0.0,
    defaultMax: 0.8
  },
  {
    id: 'ndwi',
    name: 'NDWI',
    description: 'Índice de Água',
    icon: Droplets,
    color: '#3b82f6',
    defaultMin: 0.2,
    defaultMax: 1.0
  },
  {
    id: 'popDens',
    name: 'POP_DENS',
    description: 'Densidade Populacional',
    icon: Settings,
    color: '#8b5cf6',
    defaultMin: 0,
    defaultMax: 10000
  },
  {
    id: 'nightLights',
    name: 'NIGHT_LIGHTS',
    description: 'Luzes Noturnas',
    icon: Sun,
    color: '#f59e0b',
    defaultMin: 20,
    defaultMax: 255
  }
];

const ControlPanel: React.FC<ControlPanelProps> = ({ selectedArea, onLoadingChange, onOpenDataAnalysis }) => {
  const { fetchSatelliteData, downloadData, isLoading } = useClimateData();
  const [startDate, setStartDate] = useState<Date>(new Date(Date.now() - 30 * 24 * 60 * 60 * 1000));
  const [endDate, setEndDate] = useState<Date>(new Date());
  const [selectedVariables, setSelectedVariables] = useState<string[]>(['lst', 'ndvi']);
  const [filters, setFilters] = useState<{ [key: string]: { min: string; max: string } }>({});

  useEffect(() => {
    onLoadingChange(isLoading);
  }, [isLoading, onLoadingChange]);

  const handleVariableToggle = (variableId: string) => {
    setSelectedVariables(prev => 
      prev.includes(variableId) 
        ? prev.filter(id => id !== variableId)
        : [...prev, variableId]
    );
  };

  const handleFilterChange = (variableId: string, field: 'min' | 'max', value: string) => {
    setFilters(prev => ({
      ...prev,
      [variableId]: {
        ...prev[variableId],
        [field]: value
      }
    }));
  };

  const handleUseDefaults = () => {
    const defaultFilters: { [key: string]: { min: string; max: string } } = {};
    
    selectedVariables.forEach(variableId => {
      const variable = VARIABLES.find(v => v.id === variableId);
      if (variable && variable.defaultMin !== undefined && variable.defaultMax !== undefined) {
        defaultFilters[variableId] = {
          min: variable.defaultMin.toString(),
          max: variable.defaultMax.toString()
        };
      }
    });
    
    setFilters(defaultFilters);
  };

  const handleAnalyze = async () => {
    if (!selectedArea) {
      alert('Por favor, selecione uma área no mapa primeiro.');
      return;
    }

    if (selectedVariables.length === 0) {
      alert('Por favor, selecione pelo menos uma variável para análise.');
      return;
    }

    await fetchSatelliteData(
      selectedArea.bounds,
      startDate.toISOString().split('T')[0],
      endDate.toISOString().split('T')[0],
      selectedVariables,
      selectedArea.selectionType,
      selectedArea.circleParams
    );
  };

  const handleDownload = async (format: string) => {
    if (!selectedArea) {
      alert('Por favor, selecione uma área no mapa primeiro.');
      return;
    }

    if (selectedVariables.length === 0) {
      alert('Por favor, selecione pelo menos uma variável para download.');
      return;
    }

    await downloadData(
      selectedArea.bounds,
      startDate.toISOString().split('T')[0],
      endDate.toISOString().split('T')[0],
      selectedVariables,
      format
    );
  };

  return (
    <PanelContainer>
      <Section>
        <SectionTitle>
          <Calendar size={20} />
          Período de Análise
        </SectionTitle>
        <DateRangeContainer>
          <FormGroup>
            <Label>Data Inicial</Label>
            <DateInput
              selected={startDate}
              onChange={(date: Date) => setStartDate(date)}
              dateFormat="dd/MM/yyyy"
              maxDate={endDate}
            />
          </FormGroup>
          <FormGroup>
            <Label>Data Final</Label>
            <DateInput
              selected={endDate}
              onChange={(date: Date) => setEndDate(date)}
              dateFormat="dd/MM/yyyy"
              maxDate={new Date()}
            />
          </FormGroup>
        </DateRangeContainer>
      </Section>

      <Section>
        <SectionTitle>
          <Filter size={20} />
          Variáveis Ambientais
        </SectionTitle>
        <VariableGrid>
          {VARIABLES.map(variable => {
            const IconComponent = variable.icon;
            return (
              <VariableCard
                key={variable.id}
                selected={selectedVariables.includes(variable.id)}
                onClick={() => handleVariableToggle(variable.id)}
              >
                <VariableIcon color={variable.color}>
                  <IconComponent size={16} />
                </VariableIcon>
                <VariableName>{variable.name}</VariableName>
                <VariableDesc>{variable.description}</VariableDesc>
              </VariableCard>
            );
          })}
        </VariableGrid>
      </Section>

      {selectedVariables.length > 0 && (
        <Section>
          <SectionTitle>
            <Settings size={20} />
            Filtros de Valores
          </SectionTitle>
          <FilterSection>
            <FilterHeader>
              <FilterTitle>Configure os valores mínimos e máximos para cada variável</FilterTitle>
              <DefaultButton onClick={handleUseDefaults}>
                Usar Valores Padrão
              </DefaultButton>
            </FilterHeader>
            {selectedVariables.map(variableId => {
              const variable = VARIABLES.find(v => v.id === variableId);
              if (!variable) return null;
              
              const IconComponent = variable.icon;
              
              return (
                <FilterRow key={variableId}>
                  <VariableTitle>
                    <VariableIcon color={variable.color}>
                      <IconComponent size={12} />
                    </VariableIcon>
                    {variable.name} - {variable.description}
                  </VariableTitle>
                  <FilterInputsRow>
                    <FilterInputGroup>
                      <FilterLabel>Valor Mínimo:</FilterLabel>
                      <FilterInput
                        type="number"
                        placeholder={variable.defaultMin?.toString() || "Ex: 20"}
                        value={filters[variableId]?.min || ''}
                        onChange={(e) => handleFilterChange(variableId, 'min', e.target.value)}
                      />
                    </FilterInputGroup>
                    <FilterInputGroup>
                      <FilterLabel>Valor Máximo:</FilterLabel>
                      <FilterInput
                        type="number"
                        placeholder={variable.defaultMax?.toString() || "Ex: 45"}
                        value={filters[variableId]?.max || ''}
                        onChange={(e) => handleFilterChange(variableId, 'max', e.target.value)}
                      />
                    </FilterInputGroup>
                  </FilterInputsRow>
                </FilterRow>
              );
            })}
          </FilterSection>
        </Section>
      )}

      <Section>
        <ActionButton
          variant="primary"
          onClick={handleAnalyze}
          disabled={!selectedArea || selectedVariables.length === 0 || isLoading}
        >
          <Play size={20} />
          {isLoading ? 'Analisando...' : 'Analisar Área'}
        </ActionButton>
        
        <ActionButton
          variant="secondary"
          onClick={onOpenDataAnalysis}
          disabled={!selectedArea}
          style={{ marginTop: '12px' }}
        >
          <Filter size={16} />
          Ver Análise de Dados
        </ActionButton>
      </Section>

      <DownloadSection>
        <SectionTitle>
          <Download size={20} />
          Download de Dados
        </SectionTitle>
        <DownloadButtons>
          <ActionButton
            variant="secondary"
            onClick={() => handleDownload('csv')}
            disabled={!selectedArea || selectedVariables.length === 0}
          >
            <Download size={16} />
            CSV
          </ActionButton>
          <ActionButton
            variant="secondary"
            onClick={() => handleDownload('json')}
            disabled={!selectedArea || selectedVariables.length === 0}
          >
            <Download size={16} />
            JSON
          </ActionButton>
          <ActionButton
            variant="secondary"
            onClick={() => handleDownload('png')}
            disabled={!selectedArea || selectedVariables.length === 0}
          >
            <Download size={16} />
            PNG
          </ActionButton>
        </DownloadButtons>
      </DownloadSection>
    </PanelContainer>
  );
};

export default ControlPanel;
