import React from 'react';
import styled from 'styled-components';
import { Cloud, MapPin, BarChart3, Sun, Moon } from 'lucide-react';
import { useTheme } from '../context/ThemeContext';

interface HeaderProps {
  selectionMode?: 'point' | 'rectangle' | 'circle' | null;
}

const HeaderContainer = styled.header`
  background: var(--card-background);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--border-color);
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: var(--shadow);
  min-height: 70px;
  flex-shrink: 0;
  
  @media (max-width: 768px) {
    padding: 1rem;
    flex-direction: column;
    gap: 1rem;
    min-height: auto;
  }
`;

const Logo = styled.div`
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--header-color);
  background: -webkit-linear-gradient(45deg, var(--color-min), var(--color-max));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
`;

const LogoIcon = styled.div`
  width: 40px;
  height: 40px;
  background: linear-gradient(45deg, var(--primary-color), var(--color-max));
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  box-shadow: var(--shadow);
`;

const Nav = styled.nav`
  display: flex;
  align-items: center;
  gap: 2rem;
`;

const NavItem = styled.div`
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-color);
  font-weight: 500;
  transition: color 0.2s ease;

  &:hover {
    color: var(--primary-color);
  }
`;

const StatusBadge = styled.div`
  background: var(--primary-color);
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: var(--shadow);
`;

const ThemeToggle = styled.button`
  background: var(--card-background);
  border: 1px solid var(--border-color);
  color: var(--text-color);
  padding: 0.5rem;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  box-shadow: var(--shadow);

  &:hover {
    background: var(--primary-color);
    color: white;
    transform: translateY(-1px);
  }

  &:active {
    transform: translateY(0);
  }
`;

const SelectionStatus = styled.div`
  background: linear-gradient(45deg, var(--primary-color), var(--color-max));
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.875rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: var(--shadow);
  animation: pulse 2s infinite;

  @keyframes pulse {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.8;
    }
  }
`;

const Header: React.FC<HeaderProps> = ({ selectionMode }) => {
  const { theme, toggleTheme } = useTheme();

  const getSelectionMessage = () => {
    switch (selectionMode) {
      case 'point':
        return 'Clique em qualquer lugar do mapa para selecionar um ponto';
      case 'rectangle':
        return 'Clique em dois pontos para criar um retângulo';
      case 'circle':
        return 'Clique em dois pontos para criar um círculo';
      default:
        return null;
    }
  };

  return (
    <HeaderContainer>
      <Logo>
        <LogoIcon>
          <Cloud size={24} />
        </LogoIcon>
            SOLARIS
      </Logo>
      
      <Nav>
        {selectionMode && (
          <SelectionStatus>
            <MapPin size={16} />
            {getSelectionMessage()}
          </SelectionStatus>
        )}
        <NavItem>
          <MapPin size={20} />
          Seleção de Área
        </NavItem>
        <NavItem>
          <BarChart3 size={20} />
          Análise de Dados
        </NavItem>
        <ThemeToggle onClick={toggleTheme} title={`Alternar para tema ${theme === 'dark' ? 'claro' : 'escuro'}`}>
          {theme === 'dark' ? <Sun size={20} /> : <Moon size={20} />}
        </ThemeToggle>
        <StatusBadge>
          <div style={{ width: 8, height: 8, background: 'white', borderRadius: '50%' }} />
          Online
        </StatusBadge>
      </Nav>
    </HeaderContainer>
  );
};

export default Header;
