import React, { createContext, useContext, useState, useCallback } from 'react';
import axios from 'axios';
import toast from 'react-hot-toast';

export interface ClimateDataPoint {
  lat: number;
  lon: number;
  value: number;
}

export interface ClimateData {
  [variable: string]: ClimateDataPoint[];
}

export interface RegionStats {
  [variable: string]: {
    min: number;
    max: number;
    mean: number;
    count: number;
  };
}

export interface ClimateDataContextType {
  climateData: ClimateData | null;
  regionStats: RegionStats | null;
  isLoading: boolean;
  fetchClimateData: (lat: number, lon: number, startDate: string, endDate: string, source?: string) => Promise<void>;
  fetchSatelliteData: (bounds: any, startDate: string, endDate: string, variables: string[], selectionType?: string, circleParams?: { center: [number, number]; radiusKm: number }) => Promise<void>;
  fetchRegionStats: (bounds: any, startDate: string, endDate: string, variables: string[]) => Promise<void>;
  downloadData: (bounds: any, startDate: string, endDate: string, variables: string[], format: string) => Promise<void>;
  clearData: () => void;
}

const ClimateDataContext = createContext<ClimateDataContextType | undefined>(undefined);

export const useClimateData = () => {
  const context = useContext(ClimateDataContext);
  if (!context) {
    throw new Error('useClimateData deve ser usado dentro de ClimateDataProvider');
  }
  return context;
};

export const ClimateDataProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [climateData, setClimateData] = useState<ClimateData | null>(null);
  const [regionStats, setRegionStats] = useState<RegionStats | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  const fetchClimateData = useCallback(async (
    lat: number, 
    lon: number, 
    startDate: string, 
    endDate: string, 
    source: string = 'visualcrossing'
  ) => {
    setIsLoading(true);
    try {
      const response = await axios.post('/api/climate-data', {
        lat,
        lon,
        startDate,
        endDate,
        source
      });
      
      setClimateData(response.data);
      toast.success('Dados climáticos carregados com sucesso!');
    } catch (error: any) {
      console.error('Erro ao buscar dados climáticos:', error);
      toast.error(error.response?.data?.error || 'Erro ao carregar dados climáticos');
    } finally {
      setIsLoading(false);
    }
  }, []);

  const fetchSatelliteData = useCallback(async (
    bounds: any,
    startDate: string,
    endDate: string,
    variables: string[],
    selectionType?: string,
    circleParams?: { center: [number, number]; radiusKm: number }
  ) => {
    setIsLoading(true);
    try {
      const response = await axios.post('/api/satellite-data', {
        bounds,
        startDate,
        endDate,
        variables,
        selectionType,
        circleParams
      });
      
      setClimateData(response.data);
      toast.success('Dados de satélite carregados com sucesso!');
    } catch (error: any) {
      console.error('Erro ao buscar dados de satélite:', error);
      toast.error(error.response?.data?.error || 'Erro ao carregar dados de satélite');
    } finally {
      setIsLoading(false);
    }
  }, []);

  const fetchRegionStats = useCallback(async (
    bounds: any,
    startDate: string,
    endDate: string,
    variables: string[]
  ) => {
    try {
      const response = await axios.post('/api/region-stats', {
        bounds,
        startDate,
        endDate,
        variables
      });
      
      setRegionStats(response.data);
    } catch (error: any) {
      console.error('Erro ao buscar estatísticas da região:', error);
      toast.error(error.response?.data?.error || 'Erro ao carregar estatísticas');
    }
  }, []);

  const downloadData = useCallback(async (
    bounds: any,
    startDate: string,
    endDate: string,
    variables: string[],
    format: string
  ) => {
    try {
      const response = await axios.post('/api/download', {
        bounds,
        startDate,
        endDate,
        variables,
        format
      }, {
        responseType: 'blob'
      });

      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
          link.setAttribute('download', `solaris_data.${format}`);
      document.body.appendChild(link);
      link.click();
      link.remove();
      window.URL.revokeObjectURL(url);

      toast.success(`Download realizado com sucesso! (${format.toUpperCase()})`);
    } catch (error: any) {
      console.error('Erro ao fazer download:', error);
      toast.error(error.response?.data?.error || 'Erro ao fazer download');
    }
  }, []);

  const clearData = useCallback(() => {
    setClimateData(null);
    setRegionStats(null);
  }, []);

  const value: ClimateDataContextType = {
    climateData,
    regionStats,
    isLoading,
    fetchClimateData,
    fetchSatelliteData,
    fetchRegionStats,
    downloadData,
    clearData
  };

  return (
    <ClimateDataContext.Provider value={value}>
      {children}
    </ClimateDataContext.Provider>
  );
};
